label.ios {
  display: block;
  cursor: pointer;
  width: 42px;
  height: 26px;
  border: 1px solid rgb(203, 199, 202);
  border-radius: 13px;
  transition: 1.2s;
}

.CheckBox_ios {
  position: relative;
  width: 42px;
  height: 26px;
}

div.ios {
  width: 24px;
  height: 24px;
  padding: 0 12px;
  background-color: white;
  border-radius: 100%;
  position: absolute;
  left: 0;
  top: 1;
  box-shadow: 0px 7px 14px rgb(203, 199, 202);
  transition: left 0.8s, width 0.7s 0.1s, padding 0.7s;
}

input.ios {
  display: none;
}

input:checked+label {
  transition: 1.2s;
  box-shadow: inset 0 0 7px 15px rgb(48, 243, 75);
}

input:checked+label>div {
  transition: left 1.2s, width 0.7s, padding 0.7s 0.1s;
  width: 24px;
  padding: 0;
  left: 16px;

}