.other_info {
  margin: 10px 20px;
  width: 360px;

}

.title_container {
  width: 400px;
  background-color: rgb(243, 244, 248);
  word-wrap: break-word;
}

.bigtitle {
  margin: 0 20px;
  width: 360px;
  font-size: 24px;
  font-weight: bold;
}

.detail {
  margin: 0 20px;
  width: 360px;
  font-size: 16px;
}

.option_container {
  width: 400px;
  font-size: 16px;
  background-color: rgb(254, 254, 254);
}

.option_title {
  height: 55px;
  line-height: 55px;
  width: 360px;
  margin: 0 20px;
}

.option_userImg {
  padding: 0 20px;
  width: 400px;
  background-color: rgb(243, 244, 248);
}

.option_title_main {
  height: 53px;
  display: flex;
  justify-content: space-between;
}

.spanleft {
  display: block;
  width: 40px;
  text-align: right;
}

.spanright {
  display: block;
  width: 50px;
  text-align: right;
}

.option_title_percent {
  display: block;
  height: 2px;
  background-color: rgb(24, 144, 255);
}

.vote_deadline {
  margin: 10px 20px;
  width: 360px;
  font-size: 14px;
  color: rgb(151, 152, 155);
}

.option_userImg {
  display: flex;
  min-height: 20px;
  width: 400px;
  flex-wrap: wrap;
  border-top: 1px solid rgba(255, 255, 255, 0.8);
  box-shadow: 0px 0px 12px #ffffff;
}

.user-img {
  width: 35px;
  height: 35px;
  margin: 5px;
  border-radius: 50%;
}