.Vote {
  width: 100%;
  height: 690px;
  background-color: rgb(250, 250, 250, 0.8)
}


.vote_card {
  cursor: pointer;
  width: 400px;
}

.vote_card_title {
  font-size: 20px;
  width: 400px;
  padding: 10px 20px;
  background-color: #fff;
  word-wrap: break-word;
}

.lalala {
  width: 400px;
  padding: 3px 20px;
  font-size: 12px;

}

.vote_card_end {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 400px;
  padding: 0px 20px;
  font-size: 15px;
}