.main_container {
  width: 400px;
  height: 800px;
  position: absolute;
  z-index: 0;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.9);
}

.mainHead {
  width: 100%;
  height: 60px;
  text-align: center;
  line-height: 60px;
  background-color: rgba(243, 244, 248);
}

.site-page-header {
  border: 1px solid rgb(235, 237, 240);
}

.others {
  width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 150px;
  background-color: rgba(255, 255, 255);
}

.other {
  display: flex;
  width: 360px;
  height: 50px;
  line-height: 40px;
  border-bottom: 1px solid rgb(228, 220, 220);
  justify-content: space-between;
  align-items: center;
}

#tonewvote {
  color: rgb(38, 38, 38);
  position: absolute;
  left: 10px;
  top: 10px;
}

#tonewvote:hover {
  color: rgb(37, 109, 243);
}

.option {
  display: flex;
  justify-content: space-between;
  width: 380px;
  height: 50px;
  margin: 0 10px;
  line-height: 50px;
  border-bottom: 1px solid rgb(228, 220, 220);
}

.votetitle {
  width: 380px;
  margin: 0 10px;
  border-bottom: 1px solid rgb(228, 220, 220);
}

.options {
  width: 100%;
  background-color: rgba(255, 255, 255);
}

.jian {
  font-size: 20px;
  line-height: 55px;
  color: rgb(255, 83, 83);
}

.jia {
  font-size: 20px;
  line-height: 50px;
}

.tianjia {
  cursor: pointer;
  display: flex;
  line-height: 55px;
  width: 380px;
  margin: 0 10px;
  color: rgb(37, 109, 243);
}